
.mob-menu{
  position: fixed;
  background-color: #E17992;
  top: 0;
  right: 0;
  width: 100%;
  height:  502px;
  padding-top: 60px;
  transform: translateX(100%);
  transition: transform .2s;
  z-index: 9;

  &.is-hidden{
   transform: translateX(0);
   box-shadow: (-8px 0px 20px rgba(0, 0, 0, 0.25));;
  }

  // @media screen and (min-width: 768px) {
  //   width: 254px;
  // }
}

