.mobileMenu__buttonBuyNow {
  width: 100%;
  margin-bottom: 32px;
}

.mobileMenu__item {
  display: flex;
  align-items: center;
  min-height: 42px;
  position: relative;
}

.mobileMenu__item ::before{
content: "";
position: absolute;
left: -20px;
top: 0;
width: 116%;
height: 1px;
background-color: #E18298;
}

.mobileMenu__item:last-child::after{
content: "";
position: absolute;
left: -20px;
bottom: 0;
width: 116%;
height: 1px;
background-color: #E18298;
  
}
