.mobileMenu__link {
font-family: DM Sans;
font-style: normal;
font-weight: bold;
font-size: 14px;
line-height: 18px;
color: #FFFFFF;
}


.buttonMenu {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  color: #D41443;
  border: none;
  // width: 209px;
  height: 44px;
  background: #FFFFFF;
  border-radius: 22px;
  text-align: center;
  }
  
  .buttonMenu:hover{
      background-color: #ECEBEB;
  }
  
  .buttonMenu::after{
      content: '';
      display: inline-block;
      width: 8px;
      height: 8px;
      border-right: 2px solid #D41443;
      border-top: 2px solid #D41443;
      transform: rotate(45deg);
      border-radius: 2px;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .buttonMenu:hover::after{
      transform: rotate(225deg);
  }
  
  .buttonMenu__text{
      margin-right: 10px;
  }

  