.about-wrapper__pre-title {
  font-family: Titan One;
  font-size: getFont('lable-size-mobile');
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
  color: getColor('lable-color-text');
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }
  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.about-wrapper__inner {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 26px;
  @media screen and (max-width: 768px) {
    width: 200px;
  }
  @media screen and (mix-width: 768px) {
    margin-bottom: 50px;
  }
  @media screen and (mix-width: 1200px) {
    margin-bottom: 55px;
  }
}

.about-wrapper__title {
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.14;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: getColor('title-color-main-text');
  @media screen and (min-width: 768px) {
    font-size: getFont('title-hero-mobile');
    line-height: 1.15;
  }
  @media screen and (min-width: 1200px) {
    font-size: getFont('title-hero-laptop');
    line-height: 1.15;
  }
}
@media screen and (max-width: 767px) {
  .about-wrapper__img-wrap {
    margin-bottom: 45px;
  }
}
@media screen and (min-width: 768px) {
  .about-wrapper__position {
    display: inline-flex;
    justify-content: center;
  }
}

.about-wrapper__img {
  margin: 0 auto;
  @media screen and (min-width: 768px) {
    margin-right: 17px;
  }
  @media screen and (min-width: 1200px) {
    margin-right: 75px;
  }
}

.text-inner {
  max-width: 220px;
  margin: 0 auto;
  @media screen and (min-width: 768px) {
    max-width: 340px;
  }
  @media screen and (min-width: 1200px) {
    max-width: 463px;
  }
}

.text-inner__top {
  font-family: DM Sans;
  font-weight: 500;
  font-size: getFont('mine-size-mobile');
  color: getColor('secondary-color-text');
  line-height: 1.62;
  margin-bottom: 40px;
  @media screen and (min-width: 768px) {
    font-size: getFont('secondary-size-mobile');
  }
  @media screen and (min-width: 1200px) {
    font-size: getFont('samebtn-size-mobile');
  }
}

.text-inner__midle {
  font-family: DM Sans;
  font-weight: 500;
  font-size: getFont('mine-size-mobile');
  color: getColor('gray-color-text');
  line-height: 1.62;
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    font-size: getFont('secondary-size-mobile');
  }
  @media screen and (min-width: 1200px) {
    font-size: getFont('samebtn-size-mobile');
  }
}

.text-inner__bottom {
  font-family: DM Sans;
  font-weight: 500;
  font-size: getFont('mine-size-mobile');
  color: getColor('gray-color-text');
  line-height: 1.62;
  margin-bottom: 25px;
  @media screen and (min-width: 768px) {
    font-size: getFont('secondary-size-mobile');
    margin-bottom: 60px;
  }
  @media screen and (min-width: 1200px) {
    font-size: getFont('samebtn-size-mobile');
    margin-bottom: 45px;
  }
}

.about-btn {
  display: inline-block;
  font-family: DM Sans;
  font-size: getFont('mine-size-mobile');
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;
  border-radius: 22px;
  background-color: getColor('mine-color-text');
  color: getColor('title-color-main-text');
  border: none;
  cursor: pointer;
  min-width: 175px;
  padding: 15px 56px 15px 37px;
  margin: 0 auto;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}

.about-btn::after {
  content: '';
}
